.body{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  min-height: 100vh;
  box-sizing: border-box
}

.main{
  -webkit-flex: 1 1;
          flex: 1 1;
  margin: 10px 0;
}

.app {
  margin-left: 30px;
  margin-top: 0px;
}

.appMain {
  background-color: rgba(210, 235, 13, 0.31) ;
  min-height: 500px;
}

footer {
  background-color: rgba(62, 135, 221, 0.79);
  -webkit-align-items: flex-start;
          align-items: flex-start;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  color: #fff;
  margin-top: 20;
  height: 200px;
}

.footer-main {
  margin-left: 50px;
  height: 50px;
  width: 100%;
}

footer .footer-main a{
  color: #fff;
}

footer .footer-main h2{
  text-align: left;
  font-style: italic;
  font-family: Futura,Trebuchet MS,Arial,sans-serif;
}

footer .links {
  width: 100%;
  height: 150px;
  text-align: center;
  font-family: 'Josefin Sans', sans-serif;
}

footer .links img {
  margin-left: auto;
  margin-right: 20px;
  height: 40%;
  width: auto;

}

footer .links ul {
  list-style: none;
}

footer .links ul >*{
  cursor: pointer;
  display: inline-block;
  margin-left: 0px;
  margin-right: 50px;
  padding-bottom: 0px;
  border-bottom: 1px solid white;
}

.header {
  background-color: #fff;
  color: rgb(20, 155, 213);
  height: auto;
  display: block;
  margin: 10px 10px;
}

.header-logo {
  float: left;
  width: 200px;
  padding: 10px
}

.header-logo button {
  float: left;
  display: inline;
  width: 100%;
}

.header-logo img {
  float: left;
  width: 100%;
}

.header-list button{
  cursor: pointer;
  float: left;
  border-style: none;
  background: none;
  font-size: 16px;
  font-family: 'Josefin Sans', sans-serif;
}

.header-list {
  float: right;
  padding-right: 10px;
}

.header-list li {
  float: left;
  list-style: none;
  padding: 0px 20px;
}

#headerButton {
  display: none;
}

.slides {

  position: relative;
  .slick-prev, .slick-next {
    cursor: pointer;
    position: absolute;
    top: 50%;
    z-index: 1 !important;
  }
  .slick-prev {
    left: 5% !important;
  }
  .slick-next {
    right: 5% !important;
  }
}

.title {
  font-family: sans-serif;
}

.contact-form input,textarea{
  width : 400px;
  margin-top : 10px;
  margin-bottom : 30px;
  padding : 20px;
  font-size : 18px;
  border : 1px solid #dee7ec;
}

.contact-form .submit{
  background-color : #dee7ec;
  color : #889eab;
}

.article {
  margin: 10px 30px
}

@media (max-width: 762px) {
  .headerActive {

  }
  .headerInactive{

  }
}

.news {
  list-style: none;
}

.news li button {
  cursor: pointer;
  border-style: none;
  background: none;
  text-align: left;
}

.pic-component {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  text-align: center;
}

.pic-img {
  display: block;
  width: 80%;
  margin-left: auto;
  margin-right: auto;
}

.members-card {
  height: 90px;
  width: 80%;
  height: auto;
  margin-left: auto;
  margin-right: auto;
}

.cards{
  width: 85%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  margin-left: auto;
  margin-right: auto;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.card {
  cursor : pointer;
  padding: 0px 0px;
  width: 220px;
  height: 100%;
  margin-left: auto;
  margin-right: auto;
  -webkit-flex-direction: column;
          flex-direction: column;
  border: 1px solid;
  border-radius: 4px;
  overflow: hidden;
  margin: 5px;
}

@media (max-width: 762px) {
  .cards{
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  .card{
    width: 80%;
    height: 30%;
    margin-left: auto;
    margin-right: auto;
  }
}

.card:hover{
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
}

.card-header {
  text-align: center;
  font-size: 20px;
  font-weight: 600;
  border-bottom: 1px solid;
  background-color: #FFEBEE;
  padding: 5px 0px;
}

.card-main {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 15px 0;
}

.material-icons {
  font-size: 50px;
  margin-bottom: 5px;
}

.main-description {
  font-size: 20px;
  text-align: center;
}

